<template>
  <div>
    <div id="l1"></div>
  </div>
</template>

<script>
import { registerTheme, Chart } from "@antv/g2";
export default {
  data() {
    return {
      chart: null,
      year: [
        { year: "1951 ", sales: 38 },
        { year: "1952 ", sales: 52 },
        { year: "1956 ", sales: 61 },
        { year: "1957 ", sales: 145 },
        { year: "1958 ", sales: 48 },
        { year: "1959 ", sales: 38 },
        { year: "1960 ", sales: 38 },
        { year: "1962 ", sales: 38 },
      ],
    };
  },
  mounted() {
    this.initLineChart();
  },
  methods: {
    initLineChart() {
      registerTheme("customTheme", {
        defaultColor: "red",
      });
      //new Chart()  Chart  id
      const chart = new Chart({
        container: "l1",
        autoFit: true,
        height: 500,
        syncViewPadding: true,
      });
      // 
      chart.data(this.year);
      chart.scale("sales", {
        alias: "",
      });
      chart.axis("sales", {
        title: {},
      });
      chart.scale("year", {
        alias: "",
      });
      chart.axis("year", {
        title: {},
      });
      // chart.tooltip(false);
      chart.tooltip({
        showCrosshairs: true, //  Tooltip 
      });
      // chart.interaction("active-region");
      chart.interval().position("year*sales").label("sales");
      this.chart = chart;
      this.chart.render();
    },
  },
};
</script>
<style scoped></style>
